@import "ScssHelpers/helpers-all.scss";

.promo {

	@include bp($point_2, min) {
		padding-top: 10rem;
	}
}

.promo__body {
	background-color: $beige_1;
}

.promo__row {
	display: flex;
	align-items: stretch;
	flex-wrap: wrap;
}

.promo__column {
	@include bp($point_4, min) {
		&:nth-child(1) {
			width: calc(100% - 35.7rem);
		}
		&:nth-child(2) {
			position: relative;
	
			width: 35.7rem;
	
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}
	@include bp($point_4 - 1) {
		display: flex;
		width: 100%;

		&:nth-child(2){
			order: -1;
			position: relative;
		}
	}
}

.promo__content {
	@include bp($point_4, min) {
		padding: 3.4rem 4.8rem 4.6rem;
	}
	@include bp($point_4 - 1) {
		padding: 2.4rem 2.4rem 2.8rem;
	}
}

.promo__title {
	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	@include h4;

	@include bp($point_4, min) {
		letter-spacing: -.014em;
	}
	@include bp($point_4 - 1) {
		letter-spacing: -.026em;
	}
}

.promo__text {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 1.8rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 1.4rem;
		}
	}

	@include text;

	@include bp($point_4 ,min) {
		line-height: 1.5;
	}
	@include bp($point_4 - 1) {
		line-height: 1.42;
		letter-spacing: -.016em;
	}
}

.promo_img {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;


	@include bp($point_4, min) {
		min-height: 24rem;
	}
	@include bp($point_4 - 1) {
		padding-bottom: 55.8%;
	}
}

.promo_img__in {
	@include stretch;
	@include cover_img;
}

.promo__button {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}